import React, { useState } from "react";
import ArrowIcon from "./images/icon-arrow.svg";
import LocationIcon from "./images/icon-location.svg";
import HeaderPattern from "./images/pattern-bg.png";

function App() {
  const [ipAddress, setIp] = useState([]);
  const [location, setLocation] = useState([]);
  const [timezone, setTimezone] = useState([]);
  const [isp, setIsp] = useState([]);

  const geolocationGet =
    "https://geo.ipify.org/api/v1?apiKey=at_57DZTNOmuCZILCe4Zl6FnwxCWkP2K";

  const arrowIcon = <img src={ArrowIcon} alt="Right Arrow" />;
  const locationIcon = <img src={LocationIcon} alt="Location" />;

  const divider = {
    width: "1px",
    height: "50px",
    background: "lightgrey",
  };

  fetch(geolocationGet)
    .then((res) => res.json())
    .then((data) => {
      let userIpAddress = data.ip;
      let userLocation =
        data.location.city +
        ", " +
        data.location.region +
        " " +
        data.location.postalCode;
      let userTimezone = data.location.timezone;
      let userIsp = data.isp;
      setIp(userIpAddress);
      setLocation(userLocation);
      setTimezone(userTimezone);
      setIsp(userIsp);
    });

  return (
    <div>
      <header>
        <div
          className="header-top"
          style={{ backgroundImage: "url(" + HeaderPattern + ")" }}
        >
          <h1>IP Address Tracker</h1>
          <div>
            <input
              type="text"
              placeholder="Search for any IP address or domain"
            />
            <button>{arrowIcon}</button>
          </div>
        </div>
        <div className="header-bottom">
          <div className="col">
            <p>IP Address</p>
            <h2>{ipAddress}</h2>
          </div>
          <div style={divider}></div>
          <div className="col">
            <p>Location</p>
            <h2>{location}</h2>
          </div>
          <div style={divider}></div>
          <div className="col">
            <p>Timezone</p>
            <h2>{timezone}</h2>
          </div>
          <div style={divider}></div>
          <div className="col">
            <p>ISP</p>
            <h2>{isp}</h2>
          </div>
        </div>
      </header>
    </div>
  );
}

export default App;
